.hp-available-puppies {
    padding-top: 60px;
    background-image: url('../img/available-puppies-bg.jpg');
    background-repeat: no-repeat;
    background-size: cover;
    height: 740px;
    
    &__title {
        text-align: center;
        color: $white;
        font-weight: 500;
        margin: 0;
        padding-top: 180px;
    }
    
    &__subtitle {
        max-width: 560px;
        margin-right: auto;
        margin-left: auto;
        font-family: Heebo, sans-serif;
        color: $white;
        font-weight: 400;
        text-align: center;
        margin-bottom: 0;
    }
    
    &__arrow-down {
        padding-top: 90px;
        display: flex;
        justify-content: center;
        
        a {
            background-image: url('../img/arrow-down.svg');
            background-repeat: no-repeat;
            background-size: cover;
            width: 48px;
            height: 48px;
            border-radius: 50%;
        }
    }
}
